/* General styling */
body {
  font-family: 'Arial', sans-serif;
  background-color: #6b69ba;
  margin: 0;
  padding: 20px;
}

.App {
  text-align: center;
}

.title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #04040b;
  animation: slideIn 1s ease-out forwards;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}

.input-field {
  padding: 10px;
  margin: 10px;
  width: 300px;
  border: 2px solid #ddd;
  border-radius: 5px;
  transition: border 0.3s ease, box-shadow 0.3s ease;
  font-size: 16px;
  animation: fadeIn 1.5s ease-out forwards;
}

.input-field:focus {
  border-color: #007bff;
  box-shadow: 0 0 10px rgba(0, 123, 255, 0.5);
  outline: none;
}

.submit-btn {
  padding: 12px 20px;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  animation: bounceIn 1.2s ease-out forwards;
}

.submit-btn:hover {
  background-color: #218838;
  transform: scale(1.05);
}

.entries-title {
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #080707;
}

.entries-list {
  list-style-type: none;
  padding: 0;
}

.entry-item {
  background-color: #fff;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  animation: slideUp 0.8s ease-out forwards;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
